export default {
  name: 'PillPDF',
  components: { },
  data () {
    return {
      text: 'Having used discount toner cartridges for twenty years, there have been a lot of changes in the toner cartridge market. The market today is approximately a twenty billion dollar of the discount cartridges.',
      isFavorite: 0,
      url: 'http://www.idponline.org.uk/psbesd/besd/pages/media/docs/placeholder.pdf'
    }
  },
  methods: {
    changeFavorite () {
      console.log('PillPDF Change Favorite ')
    },
    forceFileDownload (response) {
      const url = window.URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', 'file.pdf')
      // or any other extension
      document.body.appendChild(link)
      link.click()
    },
    downloadWithVueResource () {
      // const xhr = createCORSRequest('GET', this.url)
      this.$http({
        method: 'get',
        url: this.url,
        responseType: 'arraybuffer',
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'POST, GET, PUT, OPTIONS, DELETE',
          'Access-Control-Allow-Headers': 'Access-Control-Allow-Methods, Access-Control-Allow-Origin, Origin, Accept, Content-Type',
          'Content-Type': 'application/pdf'
        }
      }).then(response => {
        this.forceFileDownload(response)
      }).catch((e) => console.log(e))
    }
  }
}
